@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@900&display=swap);
body{font-family:"IBM Plex Mono", monospace;padding:0;margin:0;background:linear-gradient(to right, #ebeff1, #bac7c2, #d6d0da, #bac7c2, #c7abda, #bac7c2, #ebeff1)}.logo h1{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;color:white;width:2rem;height:2rem;border-radius:50%;background-image:linear-gradient(to left, #9b769b, #c7abda, #c4e2d1);position:absolute;left:1rem;font-family:"Dancing Script", cursive}.img-slider img{width:30rem;height:30rem;border-radius:10%}.img-slider{display:block;padding-left:2rem;padding-right:66%}.home{padding:2rem}.GalaryFoto{display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;padding:1rem;margin:2rem}li:nth-child(2n){-webkit-flex-direction:row-reverse;flex-direction:row-reverse}.GalaryFoto .picture{padding:1rem;width:18rem;height:15rem;border-radius:10%}.GalaryFoto .picture:hover{width:18.5rem;height:15.5rem;box-shadow:10px 5px 5px #646363}.main-page{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;justify-items:center;padding:1rem}.link-button{background-image:linear-gradient(to left, #9b769b, #c7abda, #c4e2d1);width:10rem;height:2rem}.link-button:hover{background-image:linear-gradient(to left, #7a557a, #663f80, #3e7254);color:white;border:2px solid #00cec9}.menu,.contact{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-flex-direction:row;flex-direction:row;justify-items:center;width:100vw;height:7vh;background-color:azure}.footer{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;position:fixed;bottom:0rem;background-color:rgba(245,245,245,0.377);width:100vw;height:7vh}.footer li:hover{background-color:rgba(170,132,132,0.781)}li{padding:1rem;list-style:none}a{text-decoration:none}.about,.home,.skills{height:83vh}.about{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;justify-items:center;padding:5rem;width:60%;height:70%}.about-text h1{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row}.about h2,h3{color:#581950}.words{color:#581950;font-size:0;line-height:1.5}.words span{font-size:5rem;display:inline-block;-webkit-animation:move 3s ease-in-out infinite;animation:move 3s ease-in-out infinite}@-webkit-keyframes move{0%{-webkit-transform:translate(-30%, 0);transform:translate(-30%, 0)}50%{text-shadow:0 25px 50px rgba(0,0,0,0.75)}100%{-webkit-transform:translate(30%, 0);transform:translate(30%, 0)}}@keyframes move{0%{-webkit-transform:translate(-30%, 0);transform:translate(-30%, 0)}50%{text-shadow:0 25px 50px rgba(0,0,0,0.75)}100%{-webkit-transform:translate(30%, 0);transform:translate(30%, 0)}}.words span:nth-child(2){-webkit-animation-delay:0.5s;animation-delay:0.5s}.words span:nth-child(3){-webkit-animation-delay:1s;animation-delay:1s}.words span:nth-child(4){-webkit-animation-delay:1.5s;animation-delay:1.5s}.words span:nth-child(5){-webkit-animation-delay:2s;animation-delay:2s}.words span:nth-child(6){-webkit-animation-delay:2.5s;animation-delay:2.5s}.words span:nth-child(7){-webkit-animation-delay:3s;animation-delay:3s}

